











































































import { Component, Vue } from "vue-property-decorator";
import api from "@/data/apis";
import { APIOwner } from "@/types/api-response";
import { Nullable } from "@/types/nullable";
import { SquareGrid } from "vue-loading-spinner";
import Stats from "@/types/stats";

@Component({
  components: {
    SquareGrid,
  },
})
export default class About extends Vue {
  owner: Nullable<APIOwner> = null;
  stats: Nullable<Stats> = null;
  waiting = true;

  async mounted(): Promise<void> {
    this.loadData();
  }

  async loadData(): Promise<void> {
    const data = await api.randomWord();
    this.owner = data.api_owner;
    const statsData = await api.getStats();
    this.stats = statsData.body;
    this.waiting = false;
  }
}
